import "core-js/modules/es.array.iterator.js";
import "core-js/modules/es.array.reduce.js";
import "core-js/modules/web.dom-collections.iterator.js";
/**
 * This is only for a check
 *
 * @module sowngwala/check
 */
import { debounce } from './utils';
var INPUT_ELEM_KEYS = ['year', 'month', 'day', 'hour', 'min', 'sec', 'lat', 'lat-bound', 'lng', 'lng-bound'];
var ELEM_KEYS = [...INPUT_ELEM_KEYS, 'ecliptic-lng', 'mean-anom', 'obliquity', 'asc', 'dec', 'azimuth', 'altitude'];
var ROUND_DIGITS = 10000;
var el = {};

// Debounce the event listener
var onchange = debounce(event_handler, 1000);

// Sowngwala functions to be dynamically imported
var GeoCoord;
var Longitude;
var Latitude;
var NaiveDateTime;
var utc_from_local_geo;
var sun_pos_horizontal;

/**
 * Executed once the page is ready.
 */
export var start = () => {
  if (typeof Sowngwala === 'undefined') throw new Error("Can't find Sowngwala");
  var ok = 1;

  // Check for required DOM elements.
  ELEM_KEYS.forEach(key => {
    el[key] = document.querySelector("#".concat(key));
    if (!el[key]) {
      ok *= 0;
    }
  });
  if (ok) {
    // Import Sowngwala's functions dynamically.
    ({
      Longitude,
      Latitude,
      GeoCoord
    } = Sowngwala.coords);
    ({
      NaiveDateTime
    } = Sowngwala.chrono);
    ({
      utc_from_local_geo
    } = Sowngwala.time);
    ({
      sun_pos_horizontal
    } = Sowngwala.sun);

    // Add event listeners for inputs
    INPUT_ELEM_KEYS.forEach(key => {
      el[key].addEventListener('input', onchange);
      el[key].addEventListener('propertychange', onchange);
    });
    onchange();
  }
};

/**
 * Calculates when data changes.
 */
function event_handler() {
  try {
    var vals = get_values();
    if (!vals) {
      console.warn('No input values?');
      return;
    }
    var lat = Latitude({
      degrees: vals.lat,
      bound: vals['lat-bound']
    });
    var lng = Longitude({
      degrees: vals.lng,
      bound: vals['lng-bound']
    });
    var geo = GeoCoord({
      lat,
      lng
    });

    // -------------------------------------
    // Local --> GST Time --> UTC Time --> UTC
    // -------------------------------------

    var dt = NaiveDateTime.from_ymd_hms(vals.year, vals.month, vals.day, vals.hour, vals.min, vals.sec);
    var utc = utc_from_local_geo(dt, geo);
    var {
      coord: horizontal,
      _equatorial: equatorial,
      _ecliptic: ecliptic,
      // Mean Anomaly (M)
      _mean_anom,
      // Mean Obliquity (ε)
      _obliquity
    } = sun_pos_horizontal(utc, geo);

    // Ecliptic Longitude (λ)
    var ecliptic_lng = ecliptic.lng;

    // Right Ascension (α)
    var asc = equatorial.asc;

    // Declination (δ)
    var dec = equatorial.dec;

    // Azimuth (A)
    var azimuth = horizontal.azimuth;

    // Altitude (α)
    var altitude = horizontal.altitude;

    // Truncating decimals for some values...

    var ecliptic_lng_fixed = Math.round(ecliptic_lng * ROUND_DIGITS) / ROUND_DIGITS;
    var asc_fixed = Math.round(asc.second() * ROUND_DIGITS) / ROUND_DIGITS;
    var dec_fixed = Math.round(dec.second() * ROUND_DIGITS) / ROUND_DIGITS;
    var azimuth_fixed = Math.round(azimuth.second() * ROUND_DIGITS) / ROUND_DIGITS;
    var altitude_fixed = Math.round(altitude.second() * ROUND_DIGITS) / ROUND_DIGITS;

    // Displaying the values to the screen...

    el['ecliptic-lng'].innerHTML = "".concat(ecliptic_lng_fixed, "\xB0");
    el['mean-anom'].innerHTML = "".concat(_mean_anom, "\xB0");
    el.obliquity.innerHTML = "".concat(_obliquity);
    el.asc.innerHTML = "".concat(asc.hour(), "\xB0").concat(asc.minute(), "'").concat(asc_fixed);
    el.dec.innerHTML = "".concat(dec.hour(), "\xB0").concat(dec.minute(), "'").concat(dec_fixed);
    el.azimuth.innerHTML = "".concat(azimuth.hour(), "\xB0").concat(azimuth.minute(), "'").concat(azimuth_fixed);
    el.altitude.innerHTML = "".concat(altitude.hour(), "\xB0").concat(altitude.minute(), "'").concat(altitude_fixed);
  } catch (err) {
    console.warn(err);
  }
}

/**
 * Get input values, and returns a collection.
 */
function get_values() {
  var ok = 1;
  var vals = INPUT_ELEM_KEYS.reduce((acc, key) => {
    var val;
    if (key.indexOf('bound') > -1) {
      val = el[key].value;
    } else {
      val = Number(el[key].value || 0);
    }
    if (typeof val === 'undefined' || val === null) {
      ok *= 0;
    }
    acc[key] = val;
    return acc;
  }, {});
  return ok ? vals : null;
}